body {
  margin: 0px ;
  padding: 0px ;
  font-family: 'Rubik', sans-serif;
  box-sizing: border-box;
  background-color: gray;
}

.font {
  color: white;
}

.white {
  height: 10px;
  background-color: rgb(82, 81, 81);
}


#upper_section {
  height: 909px;
  position: relative;
  top: 0px;
}

#upper_section::before {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0px;
  background: linear-gradient(180deg, #000 100%, #fff 0%);
  z-index: -2;
}

#bg {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  z-index: -2;
  opacity: 0.3;
}


/* Navigation Bar */


#navbar {
  display: flex;
  height: 8rem;
  position: relative;
  top: 0px;
}

/* Netflix Logo */
#navbar img {
  position: relative;
  left: 2.5rem;
}

#navright {
  display: flex;
  position: absolute;
  right: 1.5rem;
  height: 8rem;
  width: 16rem;
  justify-content: center;
}

/* Sign Up Page */

#signinpage{
    border: 2px solid black;
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.7);
    display: none;
}

#signinpage h2{
    margin-left: 50px;
    margin-top: 50px;
    font-size: 2rem;
}

#loginform{
    height: 650px;
    width: 550px;
    color: white;
    display: flex;
    flex-direction: column;
    background-color: black;
    border-radius: 5px;
    position: relative;
    border: 2px solid white;
}

#loginform form{
    display: flex;
    flex-direction: column;
}

.user{
    width: 28rem;
    height: 45px;
    margin: 20px auto;
    font-size: 1.1rem;
    border-radius: 5px;
}

#rem{
    display: flex;
    margin-left: 45px;
    color:#6d6d6d
}

#need{
    position: absolute;
    right: 50px;
    bottom: 15rem;
    cursor: pointer;
    color:#6d6d6d;
}

#bottom1{
    margin-left: 45px;
    margin-top: 50px;
    color:#6d6d6d;
    font-size: 1rem;
}

#bottom1 a{
    color: white;
    text-decoration: none;
    margin-left: 5px;
}

#bottom1 a:hover{
    text-decoration: underline;
}

#bottom2{
    margin-left: 45px;
    width: 27rem;
}

#bottom2 a{
    text-decoration: none;
    color: blue;
    margin-left: 5px;
}

#bottom2 a:hover{
    text-decoration: underline;
}

#need:hover{
    text-decoration: underline;
}

#signclick{
    width: 28rem;
    margin: 20px auto;
    height: 45px;
    background-color: red;
    font-size: 1.2rem;
    color: white;
    font-weight: 700;
    cursor: pointer;
    border-radius: 5px;
}

/* Language Select*/

#lang {
  display: flex;
  border: 2px solid white;
  background-color: black;
  height: 3rem;
  width: 8rem;
  position: relative;
  top: 2.5rem;
  font-size: 1.2rem;
  color: white;
  cursor: pointer;
  margin-left: -187px;
}

#lang option {
  color: white;
  width: 12px;
}

/* Sign In */
#navright a {
  display: flex;
  text-decoration: none;
  position: relative;
  /* left: 7rem; */
  height: 2.5rem;
  width: 7rem;
  top: 2.5rem;
  font-size: 1.5rem;
  justify-content: center;
  align-items: center;
  background-color: red;
  color: white;
  border: 2px solid black;
  border-radius: 5px;
  margin-right: -10px;
}

